.blocks{
  font-size: 0px;
  text-align: center;
  max-width: 600px;
  margin: auto;
}
.blocks .block{
  display: inline-block;
  vertical-align: middle;
  width: 48%;
  margin-right: 4%;
  margin-bottom: 70px;
  position: relative;
}
.block.about-content {
  width: 100%;
  margin: 0px;
}
.blocks .block:nth-child(2n){
  margin-right: 0px;
}
.blocks .block .block-inner{
  display: inline-block;
  vertical-align: middle;
  position: relative;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  height: 100%;
  width: 100%;
}
.blocks .block .block-inner img{
  display: block;
}
/*
.blocks .block .block-inner.icurenudity{
  border: 2px solid #000000;
}
*/
.blocks .block .block-inner .block-meta{
  position: absolute;
  left: 0px;
  top: 0px;
  display: block;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  background: rgba(255, 255, 255, 0.75);
  z-index: 2;
  -webkit-transform: scale(0);
  -moz-transform: scale(0);
  -ms-transform: scale(0);
  -o-transform: scale(0);
  transform: scale(0);
  width: 100%;
  height: 100%;
}
.blocks .block .block-inner .block-meta span{
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 80%;
  text-align: center;
  -webkit-transition: all 0.3s ease-in-out 0.4s;
  -moz-transition: all 0.3s ease-in-out 0.4s;
  -ms-transition: all 0.3s ease-in-out 0.4s;
  -o-transition: all 0.3s ease-in-out 0.4s;
  transition: all 0.3s ease-in-out 0.4s;
  opacity: 0px;
  margin-top: -30px;
  color: #000000;
  font-family: 'Biloxi';
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 3px;
}
.blocks .block .block-inner:hover .block-meta{
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}
.blocks .block .block-inner:hover .block-meta span{
  opacity: 1;
  margin: 0px;
}
