#slider{
  position: relative;
  margin: 0px 0px 50px;
}
#slider .img-meta{
  opacity:0;
}
#slider .owl-nav {
  position: fixed;
  right: 10px;
  top: 0px;
  margin: 0px;
}
#slider .owl-nav div,.grid-view{
  display: block;
  float: left;
  width: 38px;
  height: 38px;
  opacity: 1;
  cursor: pointer;
  border-bottom-width: 0px;
  color: #949494;
  font-size: 0px;
  background: transparent;
  line-height: 38px;
  padding: 0px;
  text-align: center;
}
#slider .owl-nav div:before{
  font-family: 'squarespace-ui-font';
  content: "\e02c";
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
}
#slider .owl-nav div.owl-next:before{
  content: "\e02d";
}
